  }
  .nav li .nav_pop:before{ 
       content: '.';
      font-size: 0;
      line-height: 0;
      border-width: 10px;
      border-color: inherit;
      border-top-width: 0;
      border-style: dashed;
      border-bottom-style: solid;
      border-left-color: transparent;
      border-right-color: transparent;
      border-top-color: #fff;
      border-bottom-color: #fff; 
      position: absolute;
      top: -10px;
      right: 80px;
  }
   
   
